"use client"

import { useRef, useEffect, useState, useCallback } from "react"
import ControlPanel from "../ui/control-panel"
import { TrendingUp, Ruler, Activity, RotateCcw, SkipBack, Play, Pause, SkipForward } from "lucide-react"

export default function SpringOscillator() {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const energyCanvasRef = useRef<HTMLCanvasElement>(null)

  const [isPlaying, setIsPlaying] = useState(false)
  const [time, setTime] = useState(0)
  const [layout, setLayout] = useState<"horizontal" | "vertical">("horizontal")

  const [params, setParams] = useState({
    mass: 0.5,
    springConstant: 20,
    amplitude: 80,
    phase: 0,
  })

  const historyRef = useRef<{ t: number; x: number; wd: number; wt: number; w: number }[]>([])

  const animationRef = useRef<number>()
  const timeRef = useRef(0)

  const omega = Math.sqrt(params.springConstant / params.mass)
  const period = (2 * Math.PI) / omega
  const frequency = omega / (2 * Math.PI)
  const dt = 0.016

  const calculateState = useCallback(
    (t: number) => {
      const x = params.amplitude * Math.cos(omega * t + params.phase)
      const v = -params.amplitude * omega * Math.sin(omega * t + params.phase)
      const a = -params.amplitude * omega * omega * Math.cos(omega * t + params.phase)
      const force = -params.springConstant * (x / 100)

      const x_m = x / 100
      const v_m = v / 100
      const wt = 0.5 * params.springConstant * x_m * x_m
      const wd = 0.5 * params.mass * v_m * v_m
      const wTotal = wt + wd

      return { x, v, a, force, wt, wd, wTotal }
    },
    [params, omega],
  )

  const drawSpring = (
    ctx: CanvasRenderingContext2D,
    startX: number,
    startY: number,
    endX: number,
    endY: number,
    coils = 12,
    width = 20,
  ) => {
    const dx = endX - startX
    const dy = endY - startY
    const len = Math.sqrt(dx * dx + dy * dy)
    const angle = Math.atan2(dy, dx)

    ctx.save()
    ctx.translate(startX, startY)
    ctx.rotate(angle)

    ctx.beginPath()
    ctx.moveTo(0, 0)
    ctx.lineTo(10, 0)

    const step = (len - 20) / coils
    for (let i = 0; i < coils; i++) {
      const x = 10 + i * step
      ctx.lineTo(x + step * 0.25, width)
      ctx.lineTo(x + step * 0.75, -width)
    }

    ctx.lineTo(len - 10, 0)
    ctx.lineTo(len, 0)

    ctx.stroke()
    ctx.restore()
  }

  const drawArrow = (
    ctx: CanvasRenderingContext2D,
    x: number,
    y: number,
    len: number,
    color: string,
    label: string,
  ) => {
    if (Math.abs(len) < 5) return
    ctx.save()
    ctx.strokeStyle = color
    ctx.fillStyle = color
    ctx.lineWidth = 2
    ctx.beginPath()

    const angle = layout === "horizontal" ? (len > 0 ? 0 : Math.PI) : len > 0 ? Math.PI / 2 : -Math.PI / 2

    const endX = x + (layout === "horizontal" ? len : 0)
    const endY = y + (layout === "vertical" ? len : 0)

    ctx.moveTo(x, y)
    ctx.lineTo(endX, endY)

    const headLen = 6
    ctx.translate(endX, endY)
    ctx.rotate(angle)
    ctx.moveTo(0, 0)
    ctx.lineTo(-headLen, -headLen / 2)
    ctx.lineTo(-headLen, headLen / 2)
    ctx.lineTo(0, 0)
    ctx.fill()

    ctx.font = "bold 12px sans-serif"
    ctx.fillText(label, len > 0 ? 5 : -15, -5)

    ctx.stroke()
    ctx.restore()
  }

  const draw = useCallback(
    (ctx: CanvasRenderingContext2D, t: number) => {
      const width = ctx.canvas.width
      const height = ctx.canvas.height
      const state = calculateState(t)

      ctx.clearRect(0, 0, width, height)

      ctx.strokeStyle = "#1e293b"
      ctx.lineWidth = 1
      ctx.beginPath()
      for (let i = 0; i < width; i += 40) {
        ctx.moveTo(i, 0)
        ctx.lineTo(i, height)
      }
      for (let i = 0; i < height; i += 40) {
        ctx.moveTo(0, i)
        ctx.lineTo(width, i)
      }
      ctx.stroke()

      if (layout === "horizontal") {
        const centerY = height / 2
        const wallX = 60
        const equilibriumX = width / 2
        const massX = equilibriumX + state.x

        ctx.fillStyle = "#64748b"
        ctx.fillRect(wallX - 10, centerY - 50, 10, 100)

        ctx.strokeStyle = "#475569"
        ctx.beginPath()
        ctx.moveTo(wallX, centerY + 30)
        ctx.lineTo(width, centerY + 30)
        ctx.stroke()

        ctx.strokeStyle = "rgba(250, 204, 21, 0.3)"
        ctx.setLineDash([5, 5])
        ctx.beginPath()
        ctx.moveTo(equilibriumX - params.amplitude, centerY - 60)
        ctx.lineTo(equilibriumX - params.amplitude, centerY + 60)
        ctx.moveTo(equilibriumX + params.amplitude, centerY - 60)
        ctx.lineTo(equilibriumX + params.amplitude, centerY + 60)
        ctx.stroke()
        ctx.setLineDash([])

        ctx.fillStyle = "rgba(250, 204, 21, 0.7)"
        ctx.font = "10px monospace"
        ctx.fillText("-A", equilibriumX - params.amplitude - 15, centerY - 65)
        ctx.fillText("+A", equilibriumX + params.amplitude - 5, centerY - 65)

        ctx.strokeStyle = "#4ade80"
        ctx.setLineDash([5, 5])
        ctx.beginPath()
        ctx.moveTo(equilibriumX, centerY - 70)
        ctx.lineTo(equilibriumX, centerY + 70)
        ctx.stroke()
        ctx.setLineDash([])
        ctx.fillStyle = "#4ade80"
        ctx.fillText("VTCB (O)", equilibriumX - 20, centerY - 75)

        ctx.strokeStyle = "#cbd5e1"
        ctx.lineWidth = 2
        drawSpring(ctx, wallX, centerY, massX - 25, centerY, 15, 15)

        ctx.fillStyle = "#3b82f6"
        ctx.fillRect(massX - 25, centerY - 25, 50, 50)
        ctx.strokeStyle = "#60a5fa"
        ctx.strokeRect(massX - 25, centerY - 25, 50, 50)

        drawArrow(ctx, massX, centerY, state.v * 0.5, "#22c55e", "v")
        drawArrow(ctx, massX, centerY + 35, state.force * 5, "#ef4444", "F_kv")
      } else {
        const centerX = width / 2
        const ceilingY = 40
        const equilibriumY = height / 2

        const deltaL_m = (params.mass * 9.8) / params.springConstant
        const deltaL_px = deltaL_m * 100 * 1.5

        const naturalY = equilibriumY - deltaL_px

        const massY = equilibriumY + state.x

        ctx.fillStyle = "#64748b"
        ctx.fillRect(centerX - 50, ceilingY - 10, 100, 10)

        ctx.strokeStyle = "#fbbf24"
        ctx.setLineDash([4, 4])
        ctx.beginPath()
        ctx.moveTo(centerX - 120, naturalY)
        ctx.lineTo(centerX + 120, naturalY)
        ctx.stroke()
        ctx.fillStyle = "#fbbf24"
        ctx.textAlign = "right"
        ctx.fillText("Lò xo không giãn (L0)", centerX - 130, naturalY + 4)

        ctx.strokeStyle = "#4ade80"
        ctx.beginPath()
        ctx.moveTo(centerX - 120, equilibriumY)
        ctx.lineTo(centerX + 120, equilibriumY)
        ctx.stroke()
        ctx.fillStyle = "#4ade80"
        ctx.fillText("VTCB (O)", centerX - 130, equilibriumY + 4)
        ctx.setLineDash([])

        if (deltaL_px > 10) {
          const measureX = centerX + 100
          ctx.strokeStyle = "#94a3b8"
          ctx.beginPath()
          ctx.moveTo(measureX, naturalY)
          ctx.lineTo(measureX + 10, naturalY)
          ctx.moveTo(measureX, equilibriumY)
          ctx.lineTo(measureX + 10, equilibriumY)
          ctx.moveTo(measureX + 5, naturalY)
          ctx.lineTo(measureX + 5, equilibriumY)
          ctx.stroke()

          ctx.fillStyle = "#f8fafc"
          ctx.textAlign = "left"
          ctx.fillText(`Δl₀ = ${(deltaL_m * 100).toFixed(1)} cm`, measureX + 15, naturalY + deltaL_px / 2)
        }
        ctx.textAlign = "left"

        ctx.strokeStyle = "rgba(250, 204, 21, 0.2)"
        ctx.setLineDash([2, 2])
        ctx.beginPath()
        ctx.moveTo(centerX - 60, equilibriumY - params.amplitude)
        ctx.lineTo(centerX + 60, equilibriumY - params.amplitude)
        ctx.moveTo(centerX - 60, equilibriumY + params.amplitude)
        ctx.lineTo(centerX + 60, equilibriumY + params.amplitude)
        ctx.stroke()
        ctx.setLineDash([])
        ctx.fillStyle = "rgba(250, 204, 21, 0.5)"
        ctx.font = "10px monospace"
        ctx.fillText("-A", centerX + 65, equilibriumY - params.amplitude + 3)
        ctx.fillText("+A", centerX + 65, equilibriumY + params.amplitude + 3)

        ctx.strokeStyle = "#cbd5e1"
        ctx.lineWidth = 2
        drawSpring(ctx, centerX, ceilingY, centerX, massY - 25, 12, 15)

        ctx.fillStyle = "#3b82f6"
        ctx.beginPath()
        ctx.arc(centerX, massY, 25, 0, Math.PI * 2)
        ctx.fill()
        ctx.strokeStyle = "#60a5fa"
        ctx.stroke()

        drawArrow(ctx, centerX + 30, massY, state.v * 0.5, "#22c55e", "v")
        drawArrow(ctx, centerX - 30, massY, state.force * 5, "#ef4444", "F")
      }

      ctx.fillStyle = "rgba(15, 23, 42, 0.7)"
      ctx.fillRect(10, 10, 140, 70)
      ctx.strokeStyle = "rgba(56, 189, 248, 0.3)"
      ctx.strokeRect(10, 10, 140, 70)

      ctx.fillStyle = "#4ade80"
      ctx.font = "13px monospace"
      ctx.fillText(`t = ${t.toFixed(2)} s`, 20, 30)
      ctx.fillStyle = "#38bdf8"
      ctx.fillText(`x = ${state.x.toFixed(1)} cm`, 20, 50)
      ctx.fillStyle = "#facc15"
      ctx.fillText(`v = ${state.v.toFixed(1)} cm/s`, 20, 70)
    },
    [layout, calculateState, params.amplitude, params.mass, params.springConstant],
  )

  const drawEnergyGraph = useCallback(
    (ctx: CanvasRenderingContext2D) => {
      const w = ctx.canvas.width
      const h = ctx.canvas.height
      const history = historyRef.current

      ctx.fillStyle = "#0f172a"
      ctx.fillRect(0, 0, w, h)

      ctx.strokeStyle = "#1e293b"
      ctx.lineWidth = 0.5
      ctx.beginPath()
      for (let x = 0; x < w; x += 50) {
        ctx.moveTo(x, 0)
        ctx.lineTo(x, h)
      }
      for (let y = 0; y < h; y += 30) {
        ctx.moveTo(0, y)
        ctx.lineTo(w, y)
      }
      ctx.stroke()

      if (history.length < 2) return

      const maxEnergy = 0.5 * params.springConstant * Math.pow(params.amplitude / 100, 2)
      const yScale = (h * 0.8) / (maxEnergy || 1)

      const drawLine = (key: "wd" | "wt" | "w", color: string) => {
        ctx.strokeStyle = color
        ctx.lineWidth = 2
        ctx.beginPath()
        const stepX = w / 300
        const dataSlice = history.slice(-300)

        dataSlice.forEach((pt, i) => {
          const x = i * stepX
          const y = h - pt[key] * yScale - 20
          if (i === 0) ctx.moveTo(x, y)
          else ctx.lineTo(x, y)
        })
        ctx.stroke()
      }

      drawLine("wd", "#22c55e")
      drawLine("wt", "#3b82f6")
      drawLine("w", "#eab308")
    },
    [params.springConstant, params.amplitude],
  )

  useEffect(() => {
    const render = () => {
      if (isPlaying) {
        timeRef.current += dt
        setTime(timeRef.current)

        const state = calculateState(timeRef.current)
        historyRef.current.push({
          t: timeRef.current,
          x: state.x,
          wd: state.wd,
          wt: state.wt,
          w: state.wTotal,
        })
        if (historyRef.current.length > 500) historyRef.current.shift()
      }

      if (canvasRef.current) {
        const ctx = canvasRef.current.getContext("2d")
        if (ctx) draw(ctx, timeRef.current)
      }
      if (energyCanvasRef.current) {
        const ctx = energyCanvasRef.current.getContext("2d")
        if (ctx) drawEnergyGraph(ctx)
      }

      animationRef.current = requestAnimationFrame(render)
    }
    render()
    return () => {
      if (animationRef.current) cancelAnimationFrame(animationRef.current)
    }
  }, [isPlaying, draw, calculateState, drawEnergyGraph])

  const handleStepForward = () => {
    setIsPlaying(false)
    timeRef.current += dt
    setTime(timeRef.current)
    const state = calculateState(timeRef.current)
    historyRef.current.push({ t: timeRef.current, x: state.x, wd: state.wd, wt: state.wt, w: state.wTotal })
  }

  const handleStepBackward = () => {
    setIsPlaying(false)
    timeRef.current = Math.max(0, timeRef.current - dt)
    setTime(timeRef.current)
    if (historyRef.current.length > 0) historyRef.current.pop()
  }

  const handleReset = () => {
    setIsPlaying(false)
    timeRef.current = 0
    setTime(0)
    historyRef.current = []
  }

  const deltaL = (params.mass * 9.8) / params.springConstant
  const vmax = omega * params.amplitude
  const amax = omega * omega * params.amplitude

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-4">
      <div className="lg:col-span-2 space-y-4">
        <div className="bg-[#16213e] rounded-xl overflow-hidden border border-cyan-900/50 shadow-lg relative">
          <canvas ref={canvasRef} width={800} height={400} className="w-full h-auto" />
          <div className="absolute top-2 right-2 text-xs text-gray-400 pointer-events-none">
            Mô phỏng lò xo {layout === "horizontal" ? "nằm ngang" : "thẳng đứng"}
          </div>
        </div>

        {/* UNIFIED CONTROLS */}
        <div className="flex justify-center gap-3 py-2">
          <button
            onClick={handleReset}
            className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
            title="Reset"
          >
            <RotateCcw size={20} />
          </button>
          <button
            onClick={handleStepBackward}
            className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
            title="Lùi"
          >
            <SkipBack size={20} />
          </button>
          <button
            onClick={() => setIsPlaying(!isPlaying)}
            className={`w-14 h-14 rounded-2xl flex items-center justify-center transition-all shadow-lg active:scale-95 border border-transparent ${
              isPlaying
                ? "bg-amber-600 hover:bg-amber-500 shadow-amber-900/20"
                : "bg-cyan-600 hover:bg-cyan-500 shadow-cyan-500/30"
            } text-white`}
            title={isPlaying ? "Dừng" : "Chạy"}
          >
            {isPlaying ? (
              <Pause size={28} fill="currentColor" />
            ) : (
              <Play size={28} fill="currentColor" className="ml-1" />
            )}
          </button>
          <button
            onClick={handleStepForward}
            className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
            title="Tiến"
          >
            <SkipForward size={20} />
          </button>
        </div>

        <div className="bg-[#16213e] rounded-xl p-4 border border-cyan-900/50 relative">
          <h4 className="text-sm font-bold text-orange-400 mb-2 flex items-center gap-2">
            <TrendingUp className="w-4 h-4" /> Đồ thị Năng lượng
          </h4>
          <canvas
            ref={energyCanvasRef}
            width={800}
            height={200}
            className="w-full h-40 bg-slate-900 rounded border border-slate-700/50"
          />
          <div className="flex justify-center gap-6 mt-2 text-xs font-mono">
            <div className="flex items-center gap-1">
              <div className="w-3 h-1 bg-yellow-500"></div> Cơ năng (W)
            </div>
            <div className="flex items-center gap-1">
              <div className="w-3 h-1 bg-green-500"></div> Động năng (Wđ)
            </div>
            <div className="flex items-center gap-1">
              <div className="w-3 h-1 bg-blue-500"></div> Thế năng (Wt)
            </div>
          </div>
        </div>
      </div>

      <div className="space-y-4">
        <ControlPanel title="Cấu hình hệ thống">
          <div className="flex gap-2 mb-4">
            <button
              onClick={() => setLayout("horizontal")}
              className={`flex-1 py-3 px-2 rounded-lg text-sm font-semibold transition-all border ${layout === "horizontal" ? "bg-cyan-600 border-cyan-500 text-white shadow" : "bg-slate-800 border-slate-700 text-gray-400 hover:bg-slate-700"}`}
            >
              Nằm ngang
            </button>
            <button
              onClick={() => setLayout("vertical")}
              className={`flex-1 py-3 px-2 rounded-lg text-sm font-semibold transition-all border ${layout === "vertical" ? "bg-cyan-600 border-cyan-500 text-white shadow" : "bg-slate-800 border-slate-700 text-gray-400 hover:bg-slate-700"}`}
            >
              Thẳng đứng
            </button>
          </div>

          <div className="space-y-4">
            <div>
              <label className="text-xs text-gray-400 mb-1 block">Khối lượng m</label>
              <div className="flex items-center gap-2">
                <input
                  type="range"
                  min="0.1"
                  max="2"
                  step="0.1"
                  value={params.mass}
                  onChange={(e) => setParams({ ...params, mass: Number(e.target.value) })}
                  className="flex-1 accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
                />
                <span className="w-16 text-right font-mono text-cyan-400 text-sm">{params.mass} kg</span>
              </div>
            </div>
            <div>
              <label className="text-xs text-gray-400 mb-1 block">Độ cứng k</label>
              <div className="flex items-center gap-2">
                <input
                  type="range"
                  min="10"
                  max="200"
                  step="5"
                  value={params.springConstant}
                  onChange={(e) => setParams({ ...params, springConstant: Number(e.target.value) })}
                  className="flex-1 accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
                />
                <span className="w-16 text-right font-mono text-cyan-400 text-sm">{params.springConstant} N/m</span>
              </div>
            </div>
            <div>
              <label className="text-xs text-gray-400 mb-1 block">Biên độ A</label>
              <div className="flex items-center gap-2">
                <input
                  type="range"
                  min="10"
                  max="100"
                  value={params.amplitude}
                  onChange={(e) => setParams({ ...params, amplitude: Number(e.target.value) })}
                  className="flex-1 accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
                />
                <span className="w-16 text-right font-mono text-cyan-400 text-sm">{params.amplitude} cm</span>
              </div>
            </div>
          </div>
        </ControlPanel>

        <div className="bg-[#1e293b] rounded-xl p-0 border border-cyan-900/50 overflow-hidden">
          <div className="bg-slate-800/50 p-3 border-b border-slate-700 flex items-center gap-2">
            <Activity className="w-4 h-4 text-green-400" />
            <h3 className="text-sm font-bold text-slate-200">Đại lượng dao động</h3>
          </div>
          <div className="p-4 grid grid-cols-2 gap-4">
            <div>
              <div className="text-[10px] text-gray-500 uppercase">Chu kỳ (T)</div>
              <div className="text-lg font-mono font-bold text-white">
                {period.toFixed(2)} <span className="text-xs font-normal text-gray-400">s</span>
              </div>
            </div>
            <div>
              <div className="text-[10px] text-gray-500 uppercase">Tần số (f)</div>
              <div className="text-lg font-mono font-bold text-white">
                {frequency.toFixed(2)} <span className="text-xs font-normal text-gray-400">Hz</span>
              </div>
            </div>
            <div>
              <div className="text-[10px] text-gray-500 uppercase">Vận tốc Max</div>
              <div className="text-lg font-mono font-bold text-green-400">
                {vmax.toFixed(1)} <span className="text-xs font-normal text-gray-400">cm/s</span>
              </div>
            </div>
            <div>
              <div className="text-[10px] text-gray-500 uppercase">Gia tốc Max</div>
              <div className="text-lg font-mono font-bold text-red-400">
                {amax.toFixed(0)} <span className="text-xs font-normal text-gray-400">cm/s²</span>
              </div>
            </div>
            {layout === "vertical" && (
              <div className="col-span-2 bg-slate-900 p-2 rounded border border-slate-700 flex items-center justify-between">
                <span className="text-xs text-yellow-500 flex items-center gap-1">
                  <Ruler size={12} /> Độ giãn lò xo (Δl₀)
                </span>
                <span className="font-mono font-bold text-yellow-400">{(deltaL * 100).toFixed(1)} cm</span>
              </div>
            )}
          </div>
        </div>

        <div className="bg-[#16213e] rounded-lg p-4 border border-cyan-900/50">
          <h3 className="text-cyan-400 font-semibold mb-2">Công thức</h3>
          <div className="text-sm text-gray-300 space-y-1 font-mono">
            <p>ω = √(k/m) = {omega.toFixed(2)} rad/s</p>
            <p>T = 2π√(m/k) = {period.toFixed(2)} s</p>
            <p>F = -kx (Định luật Hooke)</p>
          </div>
        </div>
      </div>
    </div>
  )
}
