"use client"

import { useRef, useEffect, useState, useCallback } from "react"
import { RotateCcw, SkipBack, Pause, Play, SkipForward } from "lucide-react"

const MAX_TIME = 20

export default function StandingWaves() {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const [isPlaying, setIsPlaying] = useState(false)
  const [time, setTime] = useState(0)
  const [endType, setEndType] = useState<"fixed-fixed" | "fixed-free">("fixed-fixed")
  const [params, setParams] = useState({
    frequency: 1,
    amplitude: 50,
    harmonic: 3,
  })

  const animationRef = useRef<number>()
  const timeRef = useRef(0)

  const omega = 2 * Math.PI * params.frequency
  const dt = 0.016

  const draw = useCallback(
    (ctx: CanvasRenderingContext2D, t: number) => {
      const width = ctx.canvas.width
      const height = ctx.canvas.height

      ctx.fillStyle = "#0f172a"
      ctx.fillRect(0, 0, width, height)

      // Grid
      ctx.strokeStyle = "#1e293b"
      ctx.lineWidth = 0.5
      for (let i = 0; i < width; i += 40) {
        ctx.beginPath()
        ctx.moveTo(i, 0)
        ctx.lineTo(i, height)
        ctx.stroke()
      }
      for (let i = 0; i < height; i += 40) {
        ctx.beginPath()
        ctx.moveTo(0, i)
        ctx.lineTo(width, i)
        ctx.stroke()
      }

      const centerY = height / 2
      const startX = 80
      const endX = width - 80
      const stringLength = endX - startX
      const n = params.harmonic
      const A = params.amplitude

      // End supports
      ctx.fillStyle = "#64748b"
      ctx.fillRect(startX - 15, centerY - 40, 15, 80)

      if (endType === "fixed-fixed") {
        ctx.fillRect(endX, centerY - 40, 15, 80)
      } else {
        ctx.strokeStyle = "#64748b"
        ctx.lineWidth = 4
        ctx.beginPath()
        ctx.arc(endX + 10, centerY, 15, 0, 2 * Math.PI)
        ctx.stroke()
      }

      // Equilibrium line
      ctx.strokeStyle = "#4ade80"
      ctx.setLineDash([5, 5])
      ctx.beginPath()
      ctx.moveTo(startX, centerY)
      ctx.lineTo(endX, centerY)
      ctx.stroke()
      ctx.setLineDash([])

      // Calculate wavelength
      let wavelength: number
      if (endType === "fixed-fixed") {
        wavelength = (2 * stringLength) / n
      } else {
        wavelength = (4 * stringLength) / (2 * n - 1)
      }
      const k = (2 * Math.PI) / wavelength

      const getEnvelope = (relX: number): number => {
        return A * Math.abs(Math.sin(k * relX))
      }

      const getWaveY = (relX: number, time: number): number => {
        return A * Math.sin(k * relX) * Math.cos(omega * time)
      }

      // Envelope
      ctx.strokeStyle = "rgba(239, 68, 68, 0.6)"
      ctx.lineWidth = 2
      ctx.setLineDash([5, 5])

      ctx.beginPath()
      for (let x = startX; x <= endX; x++) {
        const relX = x - startX
        const envelope = getEnvelope(relX)
        const y = centerY - envelope
        if (x === startX) ctx.moveTo(x, y)
        else ctx.lineTo(x, y)
      }
      ctx.stroke()

      ctx.beginPath()
      for (let x = startX; x <= endX; x++) {
        const relX = x - startX
        const envelope = getEnvelope(relX)
        const y = centerY + envelope
        if (x === startX) ctx.moveTo(x, y)
        else ctx.lineTo(x, y)
      }
      ctx.stroke()
      ctx.setLineDash([])

      // Node and antinode positions
      const nodePositions: number[] = []
      const antinodePositions: number[] = []

      if (endType === "fixed-fixed") {
        for (let m = 0; m <= n; m++) {
          const nodeX = startX + m * (wavelength / 2)
          if (nodeX <= endX + 1) nodePositions.push(nodeX)
        }
        for (let m = 0; m < n; m++) {
          const antinodeX = startX + (2 * m + 1) * (wavelength / 4)
          if (antinodeX <= endX + 1) antinodePositions.push(antinodeX)
        }
      } else {
        for (let m = 0; m < n; m++) {
          const nodeX = startX + m * (wavelength / 2)
          if (nodeX <= endX + 1) nodePositions.push(nodeX)
        }
        for (let m = 0; m < n; m++) {
          const antinodeX = startX + (2 * m + 1) * (wavelength / 4)
          if (antinodeX <= endX + 1) antinodePositions.push(antinodeX)
        }
      }

      // Wave
      ctx.strokeStyle = "#22d3ee"
      ctx.lineWidth = 3
      ctx.beginPath()
      for (let x = startX; x <= endX; x++) {
        const relX = x - startX
        const y = centerY - getWaveY(relX, t)
        if (x === startX) ctx.moveTo(x, y)
        else ctx.lineTo(x, y)
      }
      ctx.stroke()

      // Nodes (red) - STATIC at centerY
      ctx.fillStyle = "#ef4444"
      for (const nodeX of nodePositions) {
        ctx.beginPath()
        ctx.arc(nodeX, centerY, 8, 0, 2 * Math.PI)
        ctx.fill()
        ctx.strokeStyle = "#000"
        ctx.lineWidth = 2
        ctx.stroke()
      }

      // Antinodes (green) - MOVING with wave
      ctx.fillStyle = "#4ade80"
      for (const antinodeX of antinodePositions) {
        const relX = antinodeX - startX
        const antinodeY = centerY - getWaveY(relX, t)
        ctx.beginPath()
        ctx.arc(antinodeX, antinodeY, 8, 0, 2 * Math.PI)
        ctx.fill()
        ctx.strokeStyle = "#000"
        ctx.lineWidth = 2
        ctx.stroke()
      }

      // Info panel
      ctx.fillStyle = "rgba(15, 23, 42, 0.8)"
      ctx.fillRect(10, 10, 170, 100)
      ctx.strokeStyle = "rgba(56, 189, 248, 0.3)"
      ctx.strokeRect(10, 10, 170, 100)

      ctx.fillStyle = "#fff"
      ctx.font = "13px monospace"
      ctx.fillText(`t = ${t.toFixed(2)} s`, 20, 30)
      ctx.fillText(`Họa âm bậc n = ${n}`, 20, 50)
      ctx.fillText(`λ = ${wavelength.toFixed(0)} px`, 20, 70)
      ctx.fillText(endType === "fixed-fixed" ? `L = nλ/2` : `L = (2n-1)λ/4`, 20, 90)

      // Legend
      ctx.fillStyle = "#fff"
      ctx.font = "12px monospace"
      ctx.fillText(
        endType === "fixed-fixed" ? "Hai đầu cố định" : "Một đầu cố định - Một đầu tự do",
        startX,
        height - 45,
      )
      ctx.fillStyle = "#ef4444"
      ctx.fillText("● Nút (N) - đứng yên", startX + 220, height - 45)
      ctx.fillStyle = "#4ade80"
      ctx.fillText("● Bụng (B) - dao động", startX + 420, height - 45)

      ctx.fillStyle = "rgba(255, 255, 255, 0.15)"
      ctx.font = "12px monospace"
      ctx.textAlign = "right"
      ctx.fillText("Hô Hoàng Anh A1K50", width - 15, height - 15)
      ctx.textAlign = "left"
    },
    [endType, params.harmonic, params.amplitude, params.frequency, omega],
  )

  const animate = useCallback(() => {
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return

    timeRef.current += dt
    if (timeRef.current > MAX_TIME) timeRef.current = MAX_TIME
    setTime(timeRef.current)
    draw(ctx, timeRef.current)

    if (isPlaying && timeRef.current < MAX_TIME) {
      animationRef.current = requestAnimationFrame(animate)
    } else if (timeRef.current >= MAX_TIME) {
      setIsPlaying(false)
    }
  }, [isPlaying, draw])

  useEffect(() => {
    if (isPlaying) {
      animationRef.current = requestAnimationFrame(animate)
    }
    return () => {
      if (animationRef.current) cancelAnimationFrame(animationRef.current)
    }
  }, [isPlaying, animate])

  useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return
    draw(ctx, timeRef.current)
  }, [params, endType, draw])

  const handleStep = (direction: number) => {
    setIsPlaying(false)
    timeRef.current = Math.max(0, Math.min(MAX_TIME, timeRef.current + dt * direction * 10))
    setTime(timeRef.current)
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return
    draw(ctx, timeRef.current)
  }

  const handleReset = () => {
    setIsPlaying(false)
    timeRef.current = 0
    setTime(0)
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return
    draw(ctx, 0)
  }

  const handleTimelineChange = (newTime: number) => {
    setIsPlaying(false)
    timeRef.current = newTime
    setTime(newTime)
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return
    draw(ctx, newTime)
  }

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-4">
      <div className="lg:col-span-2 space-y-4">
        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50">
          <canvas ref={canvasRef} width={800} height={400} className="w-full rounded-lg bg-[#0f172a]" />
        </div>

        <div className="bg-[#1e293b] rounded-xl p-3 border border-slate-700/50 flex items-center gap-3">
          <div className="flex items-center gap-2">
            <button
              onClick={handleReset}
              className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
              title="Reset"
            >
              <RotateCcw size={20} />
            </button>
            <button
              onClick={() => handleStep(-1)}
              className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
              title="Lùi"
            >
              <SkipBack size={20} />
            </button>
            <button
              onClick={() => setIsPlaying(!isPlaying)}
              className={`w-14 h-14 rounded-2xl flex items-center justify-center transition-all shadow-lg active:scale-95 border border-transparent ${
                isPlaying
                  ? "bg-amber-500 hover:bg-amber-400 shadow-amber-900/20"
                  : "bg-cyan-500 hover:bg-cyan-400 shadow-cyan-500/30"
              } text-white`}
              title={isPlaying ? "Dừng" : "Chạy"}
            >
              {isPlaying ? (
                <Pause size={28} fill="currentColor" />
              ) : (
                <Play size={28} fill="currentColor" className="ml-1" />
              )}
            </button>
            <button
              onClick={() => handleStep(1)}
              className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
              title="Tiến"
            >
              <SkipForward size={20} />
            </button>
          </div>

          <span className="text-xs text-slate-400 font-mono w-8">0s</span>
          <input
            type="range"
            min="0"
            max={MAX_TIME}
            step="0.01"
            value={time}
            onChange={(e) => handleTimelineChange(Number(e.target.value))}
            className="flex-1 h-2 bg-slate-700 rounded-lg appearance-none cursor-pointer accent-cyan-500"
          />
          <span className="text-xs text-slate-400 font-mono w-10 text-right">{MAX_TIME}s</span>
        </div>
      </div>

      <div className="space-y-4">
        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50">
          <h3 className="text-cyan-400 font-semibold mb-3 text-sm">Điều kiện biên</h3>
          <div className="flex flex-col gap-2">
            <button
              onClick={() => setEndType("fixed-fixed")}
              className={`py-3 px-4 rounded-lg text-sm font-semibold transition-all border ${
                endType === "fixed-fixed"
                  ? "bg-cyan-600 border-cyan-500 text-white shadow"
                  : "bg-slate-800 border-slate-700 text-slate-400 hover:bg-slate-700"
              }`}
            >
              Hai đầu cố định
            </button>
            <button
              onClick={() => setEndType("fixed-free")}
              className={`py-3 px-4 rounded-lg text-sm font-semibold transition-all border ${
                endType === "fixed-free"
                  ? "bg-cyan-600 border-cyan-500 text-white shadow"
                  : "bg-slate-800 border-slate-700 text-slate-400 hover:bg-slate-700"
              }`}
            >
              Một đầu cố định - Một đầu tự do
            </button>
          </div>
        </div>

        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50">
          <h3 className="text-cyan-400 font-semibold mb-4 text-sm">Thông số</h3>
          <div className="space-y-4">
            <div>
              <div className="flex justify-between text-xs mb-1">
                <label className="text-slate-400">Họa âm bậc n</label>
                <span className="text-cyan-400 font-mono">n = {params.harmonic}</span>
              </div>
              <input
                type="range"
                min="1"
                max="7"
                value={params.harmonic}
                onChange={(e) => setParams({ ...params, harmonic: Number(e.target.value) })}
                className="w-full accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
              />
            </div>
            <div>
              <div className="flex justify-between text-xs mb-1">
                <label className="text-slate-400">Biên độ A</label>
                <span className="text-cyan-400 font-mono">{params.amplitude} px</span>
              </div>
              <input
                type="range"
                min="20"
                max="80"
                value={params.amplitude}
                onChange={(e) => setParams({ ...params, amplitude: Number(e.target.value) })}
                className="w-full accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
              />
            </div>
            <div>
              <div className="flex justify-between text-xs mb-1">
                <label className="text-slate-400">Tần số f</label>
                <span className="text-cyan-400 font-mono">{params.frequency} Hz</span>
              </div>
              <input
                type="range"
                min="0.3"
                max="2"
                step="0.1"
                value={params.frequency}
                onChange={(e) => setParams({ ...params, frequency: Number(e.target.value) })}
                className="w-full accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
              />
            </div>
          </div>
        </div>

        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50">
          <h3 className="text-cyan-400 font-semibold mb-2 text-sm">Công thức sóng dừng</h3>
          <div className="text-xs text-slate-300 space-y-2 font-mono">
            {endType === "fixed-fixed" ? (
              <>
                <p className="text-yellow-400">Hai đầu cố định:</p>
                <p>L = n·λ/2 (n = 1, 2, 3...)</p>
                <p>f_n = n·f₁</p>
                <p>Số bụng = n, Số nút = n + 1</p>
              </>
            ) : (
              <>
                <p className="text-yellow-400">Một đầu cố định:</p>
                <p>L = (2n-1)·λ/4 (n = 1, 2, 3...)</p>
                <p>f_n = (2n-1)·f₁</p>
                <p>Số bụng = n, Số nút = n</p>
              </>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}
