"use client"

import { useRef, useEffect, useState, useCallback } from "react"
import { Activity, Zap, TrendingUp, Anchor, RotateCcw, SkipBack, Pause, Play, SkipForward, Settings, Ruler, Timer } from "lucide-react"

// --- COMPONENT CON: BẢNG ĐIỀU KHIỂN ---
const ControlPanel = ({ title, icon: Icon, children }: { title: string; icon?: any; children: React.ReactNode }) => (
  <div className="bg-[#1e293b] rounded-xl border border-slate-700/50 shadow-sm overflow-hidden flex flex-col h-full">
    <div className="bg-slate-800/50 px-4 py-3 border-b border-slate-700/50 flex items-center gap-2">
      {Icon && <Icon size={16} className="text-cyan-400"/>}
      <h3 className="text-slate-300 font-bold text-xs uppercase tracking-wider">{title}</h3>
    </div>
    <div className="p-4 space-y-4 flex-1">{children}</div>
  </div>
)

// --- COMPONENT CON: Ô THÔNG SỐ NHỎ ---
const StatBox = ({ label, value, unit, color = "text-slate-200" }: { label: string, value: string, unit?: string, color?: string }) => (
    <div className="bg-slate-900/50 rounded-lg p-2 border border-slate-700/50 flex flex-col items-center justify-center min-w-[80px]">
        <div className="text-[10px] text-slate-500 uppercase font-semibold mb-1">{label}</div>
        <div className={`text-lg font-mono font-bold ${color}`}>
            {value} <span className="text-xs font-normal text-slate-500">{unit}</span>
        </div>
    </div>
)

export default function SimplePendulum() {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const energyCanvasRef = useRef<HTMLCanvasElement>(null)
  
  const [isPlaying, setIsPlaying] = useState(false)
  const [time, setTime] = useState(0)
  const MAX_TIME = 20
  
  const [params, setParams] = useState({
    length: 100,      // cm
    gravity: 9.8,     // m/s²
    initialAngle: 30, // degrees
    mass: 1.0         // kg
  })
  
  const traceRef = useRef<{x: number, y: number}[]>([])
  const energyHistoryRef = useRef<{t: number, wd: number, wt: number, w: number}[]>([])

  const animationRef = useRef<number>()
  const timeRef = useRef(0)

  // --- TÍNH TOÁN VẬT LÝ ---
  const lengthM = params.length / 100 
  const omega = Math.sqrt(params.gravity / lengthM)
  const period = (2 * Math.PI) / omega
  const frequency = 1 / period
  const thetaMax = (params.initialAngle * Math.PI) / 180
  const dt = 0.016

  const calculateState = useCallback((t: number) => {
      const theta = thetaMax * Math.cos(omega * t)
      const alphaDeg = (theta * 180) / Math.PI
      
      const omegaInst = -thetaMax * omega * Math.sin(omega * t) 
      const v = lengthM * omegaInst // m/s
      
      return { theta, alphaDeg, v }
  }, [thetaMax, omega, lengthM])

  // --- VẼ MÔ PHỎNG ---
  const drawSim = useCallback((ctx: CanvasRenderingContext2D, t: number) => {
      const width = ctx.canvas.width
      const height = ctx.canvas.height
      const centerX = width / 2
      const pivotY = 40

      ctx.clearRect(0,0, width, height)

      // Grid
      ctx.strokeStyle = "#1e293b"
      ctx.lineWidth = 0.5
      ctx.beginPath()
      for (let i = 0; i < width; i += 40) { ctx.moveTo(i, 0); ctx.lineTo(i, height) }
      for (let i = 0; i < height; i += 40) { ctx.moveTo(0, i); ctx.lineTo(width, i) }
      ctx.stroke()

      // Trục đối xứng
      ctx.strokeStyle = "#475569"
      ctx.setLineDash([6, 6])
      ctx.beginPath(); ctx.moveTo(centerX, pivotY); ctx.lineTo(centerX, height - 30); ctx.stroke(); ctx.setLineDash([])

      const state = calculateState(t)
      const visualLength = Math.min(width, height) * 0.65 
      const bobX = centerX + visualLength * Math.sin(state.theta)
      const bobY = pivotY + visualLength * Math.cos(state.theta)

      // Trace
      if (traceRef.current.length > 1) {
          ctx.strokeStyle = "rgba(244, 63, 94, 0.4)" // Rose
          ctx.lineWidth = 4
          ctx.lineCap = "round"
          ctx.beginPath()
          ctx.moveTo(traceRef.current[0].x, traceRef.current[0].y)
          for (let i = 1; i < traceRef.current.length; i++) {
              ctx.lineTo(traceRef.current[i].x, traceRef.current[i].y)
          }
          ctx.stroke()
      }

      // Con lắc
      ctx.fillStyle = "#64748b"
      ctx.fillRect(centerX - 40, pivotY - 10, 80, 10) // Giá treo

      ctx.strokeStyle = "#cbd5e1"
      ctx.lineWidth = 2
      ctx.beginPath(); ctx.moveTo(centerX, pivotY); ctx.lineTo(bobX, bobY); ctx.stroke() // Dây

      const gradient = ctx.createRadialGradient(bobX - 5, bobY - 5, 2, bobX, bobY, 25)
      gradient.addColorStop(0, "#fb923c")
      gradient.addColorStop(1, "#ea580c")
      ctx.fillStyle = gradient
      ctx.beginPath(); ctx.arc(bobX, bobY, 20, 0, 2 * Math.PI); ctx.fill()
      ctx.strokeStyle = "#fff7ed"; ctx.lineWidth = 2; ctx.stroke()

      // Góc alpha
      ctx.fillStyle = "#4ade80"
      ctx.font = "bold 14px monospace"
      ctx.textAlign = "center"
      ctx.fillText(`α = ${state.alphaDeg.toFixed(1)}°`, bobX, bobY + 45)

      // INFO PANEL TRÊN CANVAS
      ctx.fillStyle = "rgba(15, 23, 42, 0.8)"
      ctx.fillRect(10, height - 100, 160, 90)
      ctx.strokeStyle = "rgba(56, 189, 248, 0.3)"
      ctx.strokeRect(10, height - 100, 160, 90)
      
      ctx.textAlign = "left"
      ctx.font = "13px monospace"
      
      ctx.fillStyle = "#e2e8f0"
      ctx.fillText(`t = ${t.toFixed(2)} s`, 20, height - 75)
      
      ctx.fillStyle = "#22d3ee"
      ctx.fillText(`α = ${state.alphaDeg.toFixed(1)}°`, 20, height - 55)
      
      ctx.fillStyle = "#facc15" 
      ctx.fillText(`v = ${(state.v * 100).toFixed(1)} cm/s`, 20, height - 35)
      
      ctx.fillStyle = "#a78bfa"
      ctx.fillText(`ω = ${omega.toFixed(2)} rad/s`, 20, height - 15)

      // Tác giả (Đã chỉnh sửa: Nhỏ và mờ hơn)
      ctx.fillStyle = "rgba(255, 255, 255, 0.1)" // Opacity thấp hơn (0.1)
      ctx.font = "12px sans-serif" // Font nhỏ hơn (12px), bỏ in đậm
      ctx.textAlign = "right"
      ctx.fillText("Hô Hoàng Anh A1K50", width - 15, height - 15)

  }, [calculateState, omega])

  // --- VẼ ĐỒ THỊ ---
  const drawEnergyGraph = useCallback((ctx: CanvasRenderingContext2D) => {
      const w = ctx.canvas.width
      const h = ctx.canvas.height
      const history = energyHistoryRef.current
      
      ctx.fillStyle = "#0f172a"
      ctx.fillRect(0, 0, w, h)
      
      ctx.strokeStyle = "#1e293b"
      ctx.lineWidth = 0.5
      ctx.beginPath()
      for(let x=0; x<w; x+=50) { ctx.moveTo(x,0); ctx.lineTo(x,h) }
      for(let y=0; y<h; y+=25) { ctx.moveTo(0,y); ctx.lineTo(w,y) }
      ctx.stroke()

      if (history.length < 2) return

      const maxE = Math.max(...history.map(pt => pt.w)) || 1
      const yScale = (h - 30) / (maxE * 1.2)

      const drawLine = (key: 'wd'|'wt'|'w', color: string, width: number = 2) => {
          ctx.strokeStyle = color
          ctx.lineWidth = width
          ctx.beginPath()
          const dataToDraw = history.slice(-400)
          const stepX = w / (dataToDraw.length - 1 || 1)
          dataToDraw.forEach((pt, i) => {
              const x = i * stepX
              const y = h - 15 - (pt[key] * yScale)
              if (i===0) ctx.moveTo(x, y); else ctx.lineTo(x, y)
          })
          ctx.stroke()
      }

      drawLine('wt', '#3b82f6')
      drawLine('wd', '#22c55e')
      drawLine('w', '#eab308', 2.5)
  }, [])

  // --- ANIMATION ---
  const animate = useCallback(() => {
    if (!canvasRef.current) return
    const ctx = canvasRef.current.getContext("2d")
    if (!ctx) return

    if (isPlaying) {
        timeRef.current += dt
        if (timeRef.current > MAX_TIME) timeRef.current = 0
        setTime(timeRef.current)
        
        const state = calculateState(timeRef.current)
        
        const width = ctx.canvas.width
        const height = ctx.canvas.height
        const visualLength = Math.min(width, height) * 0.65
        const bobX = (width/2) + visualLength * Math.sin(state.theta)
        const bobY = 40 + visualLength * Math.cos(state.theta)
        
        traceRef.current.push({x: bobX, y: bobY})
        if (traceRef.current.length > 200) traceRef.current.shift()

        const wt = params.mass * 9.8 * lengthM * (1 - Math.cos(state.theta))
        const wd = 0.5 * params.mass * state.v * state.v
        const wTotal = wt + wd
        
        energyHistoryRef.current.push({ t: timeRef.current, wd, wt, w: wTotal })
        if (energyHistoryRef.current.length > 400) energyHistoryRef.current.shift()
    }

    drawSim(ctx, timeRef.current)
    if (energyCanvasRef.current) drawEnergyGraph(energyCanvasRef.current.getContext("2d")!)

    animationRef.current = requestAnimationFrame(animate)
  }, [isPlaying, drawSim, drawEnergyGraph, calculateState, params, lengthM])

  useEffect(() => {
    animationRef.current = requestAnimationFrame(animate)
    return () => { if (animationRef.current) cancelAnimationFrame(animationRef.current) }
  }, [animate])

  useEffect(() => {
    if (!isPlaying) {
      if (canvasRef.current) drawSim(canvasRef.current.getContext("2d")!, timeRef.current)
      if (energyCanvasRef.current) drawEnergyGraph(energyCanvasRef.current.getContext("2d")!)
    }
  }, [params, isPlaying, drawSim, drawEnergyGraph])

  const handleReset = () => { setIsPlaying(false); timeRef.current = 0; setTime(0); traceRef.current = []; energyHistoryRef.current = []; }
  const handleStep = (dir: number) => { setIsPlaying(false); timeRef.current = Math.max(0, timeRef.current + dir * 0.1); setTime(timeRef.current); }

  const currState = calculateState(time)
  const currWt = params.mass * 9.8 * lengthM * (1 - Math.cos(currState.theta))
  const currWd = 0.5 * params.mass * currState.v * currState.v
  const currW = currWt + currWd

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-4">
      {/* CỘT TRÁI */}
      <div className="lg:col-span-2 space-y-4">
        {/* Visualizer */}
        <div className="bg-[#1e293b] rounded-xl border border-slate-700/50 shadow-lg relative overflow-hidden">
            <canvas ref={canvasRef} width={800} height={380} className="w-full h-[380px] cursor-crosshair bg-[#0f172a]" />
            <div className="absolute top-4 left-4 bg-slate-900/80 backdrop-blur px-3 py-2 rounded-lg border border-slate-700 text-xs font-mono text-cyan-400 flex gap-2">
                <Activity size={14}/> Mô phỏng thời gian thực
            </div>
        </div>

        {/* Controls & Timeline */}
        <div className="bg-[#1e293b] rounded-xl p-3 border border-slate-700/50 shadow-md flex items-center gap-4">
            <div className="flex items-center gap-2">
                <button onClick={handleReset} className="w-10 h-10 rounded-lg bg-slate-700 hover:bg-slate-600 text-slate-300 flex items-center justify-center transition-all active:scale-95"><RotateCcw size={18}/></button>
                <button onClick={() => handleStep(-1)} className="w-10 h-10 rounded-lg bg-slate-700 hover:bg-slate-600 text-slate-300 flex items-center justify-center transition-all active:scale-95"><SkipBack size={18}/></button>
                <button onClick={() => setIsPlaying(!isPlaying)} className={`w-12 h-12 rounded-xl flex items-center justify-center transition-all shadow-lg active:scale-95 ${isPlaying ? 'bg-amber-500 hover:bg-amber-400 text-white' : 'bg-cyan-500 hover:bg-cyan-400 text-white'}`}>
                    {isPlaying ? <Pause size={24} fill="currentColor"/> : <Play size={24} fill="currentColor" className="ml-1"/>}
                </button>
                <button onClick={() => handleStep(1)} className="w-10 h-10 rounded-lg bg-slate-700 hover:bg-slate-600 text-slate-300 flex items-center justify-center transition-all active:scale-95"><SkipForward size={18}/></button>
            </div>
            <div className="flex-1 flex items-center gap-3 bg-slate-800/50 px-4 py-2 rounded-lg border border-slate-700/30">
                <span className="text-xs font-mono text-slate-400 w-8">0s</span>
                <div className="relative flex-1 h-2 bg-slate-700 rounded-full">
                    <div className="absolute top-0 left-0 h-full bg-cyan-500 rounded-full" style={{width: `${(time/MAX_TIME)*100}%`}}></div>
                    <div className="absolute top-1/2 -translate-y-1/2 w-4 h-4 bg-cyan-400 rounded-full shadow-md border-2 border-slate-800" style={{left: `calc(${(time/MAX_TIME)*100}% - 8px)`}}></div>
                    <input type="range" min="0" max={MAX_TIME} step="0.01" value={time} onChange={(e) => { setIsPlaying(false); timeRef.current = Number(e.target.value); setTime(timeRef.current); traceRef.current = []; }} className="absolute inset-0 w-full h-full opacity-0 cursor-pointer"/>
                </div>
                <span className="text-xs font-mono text-slate-400 w-8 text-right">{MAX_TIME}s</span>
            </div>
        </div>

        {/* Graph */}
        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50 shadow-md">
            <div className="flex justify-between items-center mb-2">
                <h3 className="text-xs font-bold text-yellow-500 uppercase flex items-center gap-2"><TrendingUp size={14}/> Đồ thị Năng lượng</h3>
                <div className="flex gap-3 text-[10px] font-bold uppercase">
                    <div className="flex items-center gap-1 text-[#eab308]"><div className="w-2 h-2 rounded-full bg-[#eab308]"></div> Cơ năng</div>
                    <div className="flex items-center gap-1 text-[#22c55e]"><div className="w-2 h-2 rounded-full bg-[#22c55e]"></div> Động năng</div>
                    <div className="flex items-center gap-1 text-[#3b82f6]"><div className="w-2 h-2 rounded-full bg-[#3b82f6]"></div> Thế năng</div>
                </div>
            </div>
            <div className="relative w-full h-32 bg-[#0f172a] rounded-lg border border-slate-700 overflow-hidden">
                <canvas ref={energyCanvasRef} width={800} height={128} className="w-full h-full"/>
            </div>
        </div>
      </div>

      {/* CỘT PHẢI */}
      <div className="lg:col-span-1 space-y-4 flex flex-col">
        <ControlPanel title="Đại lượng đo đạc" icon={Activity}>
            <div className="grid grid-cols-2 gap-2">
                <StatBox label="Thời gian" value={time.toFixed(2)} unit="s" />
                <StatBox label="Vị trí (α)" value={currState.alphaDeg.toFixed(1)} unit="°" color="text-cyan-400"/>
                <StatBox label="Chu kỳ" value={period.toFixed(2)} unit="s" color="text-purple-400"/>
                <StatBox label="Tần số" value={frequency.toFixed(2)} unit="Hz" color="text-purple-400"/>
            </div>
            <div className="space-y-1 mt-2">
                <div className="bg-green-900/20 px-3 py-1.5 rounded border border-green-500/20 flex justify-between items-center">
                    <span className="text-[10px] text-green-500 font-bold uppercase">Động năng</span>
                    <span className="font-mono font-bold text-green-400">{currWd.toFixed(3)} J</span>
                </div>
                <div className="bg-blue-900/20 px-3 py-1.5 rounded border border-blue-500/20 flex justify-between items-center">
                    <span className="text-[10px] text-blue-500 font-bold uppercase">Thế năng</span>
                    <span className="font-mono font-bold text-blue-400">{currWt.toFixed(3)} J</span>
                </div>
                <div className="bg-yellow-900/20 px-3 py-2 rounded border border-yellow-500/20 flex justify-between items-center mt-1">
                    <span className="text-[10px] text-yellow-500 font-bold uppercase">Cơ năng</span>
                    <span className="font-mono font-bold text-yellow-400 text-lg">{currW.toFixed(3)} J</span>
                </div>
            </div>
        </ControlPanel>

        <ControlPanel title="Cấu hình hệ thống" icon={Settings}>
            <div className="space-y-5">
                <div>
                    <div className="flex justify-between text-xs text-slate-400 mb-1"><span className="flex items-center gap-1"><Ruler size={12}/> Chiều dài (l)</span> <span className="text-cyan-400 font-mono font-bold">{params.length} cm</span></div>
                    <input type="range" min="50" max="250" value={params.length} onChange={(e) => { setParams({ ...params, length: Number(e.target.value) }); traceRef.current = []; energyHistoryRef.current = []; }} className="w-full accent-cyan-500 h-1.5 bg-slate-700 rounded-lg cursor-pointer"/>
                </div>
                <div>
                    <div className="flex justify-between text-xs text-slate-400 mb-1"><span className="flex items-center gap-1"><Anchor size={12}/> Gia tốc (g)</span> <span className="text-cyan-400 font-mono font-bold">{params.gravity} m/s²</span></div>
                    <input type="range" min="1.6" max="20" step="0.1" value={params.gravity} onChange={(e) => { setParams({ ...params, gravity: Number(e.target.value) }); traceRef.current = []; energyHistoryRef.current = []; }} className="w-full accent-cyan-500 h-1.5 bg-slate-700 rounded-lg cursor-pointer"/>
                </div>
                <div>
                    <div className="flex justify-between text-xs text-slate-400 mb-1"><span className="flex items-center gap-1"><RotateCcw size={12}/> Góc đầu (α₀)</span> <span className="text-cyan-400 font-mono font-bold">{params.initialAngle}°</span></div>
                    <input type="range" min="5" max="60" value={params.initialAngle} onChange={(e) => { setParams({ ...params, initialAngle: Number(e.target.value) }); traceRef.current = []; energyHistoryRef.current = []; }} className="w-full accent-cyan-500 h-1.5 bg-slate-700 rounded-lg cursor-pointer"/>
                </div>
                <div>
                    <div className="flex justify-between text-xs text-slate-400 mb-1"><span className="flex items-center gap-1"><Zap size={12}/> Khối lượng (m)</span> <span className="text-cyan-400 font-mono font-bold">{params.mass} kg</span></div>
                    <input type="range" min="0.1" max="5" step="0.1" value={params.mass} onChange={(e) => { setParams({ ...params, mass: Number(e.target.value) }); energyHistoryRef.current = []; }} className="w-full accent-cyan-500 h-1.5 bg-slate-700 rounded-lg cursor-pointer"/>
                </div>
            </div>
            <div className="mt-4 pt-4 border-t border-slate-700/50 text-xs text-slate-400 font-mono">
                <p>T = 2π√(l/g)</p>
                <p>W = mgl(1-cos(α₀))</p>
            </div>
        </ControlPanel>
      </div>
    </div>
  )
}