"use client"

import { useState } from "react"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import HarmonicOscillation from "./experiments/harmonic-oscillation"
import SpringOscillator from "./experiments/spring-oscillator"
import SimplePendulum from "./experiments/simple-pendulum"
import DampedOscillation from "./experiments/damped-oscillation"
import MechanicalWaves from "./experiments/mechanical-waves"
import SoundWaves from "./experiments/sound-waves"
import WaveInterference from "./experiments/wave-interference"
import StandingWaves from "./experiments/standing-waves"

export default function PhysicsLab() {
  const [activeTab, setActiveTab] = useState("harmonic")

  const experiments = [
    { id: "harmonic", label: "Dao động điều hòa", component: HarmonicOscillation },
    { id: "spring", label: "Con lắc lò xo", component: SpringOscillator },
    { id: "pendulum", label: "Con lắc đơn", component: SimplePendulum },
    { id: "damped", label: "Dao động tắt dần", component: DampedOscillation },
    { id: "mechanical", label: "Sóng cơ", component: MechanicalWaves },
    { id: "sound", label: "Sóng âm", component: SoundWaves },
    { id: "interference", label: "Giao thoa sóng", component: WaveInterference },
    { id: "standing", label: "Sóng dừng", component: StandingWaves },
  ]

  return (
    <div className="min-h-screen bg-[#1a1a2e] text-gray-100">
      <header className="border-b border-cyan-900/50 bg-[#16213e] px-6 py-4">
        <h1 className="text-2xl font-bold text-cyan-400">🔬 Phòng thí nghiệm Vật lý Ảo - Lớp 11</h1>
        <p className="text-sm text-gray-400 mt-1">Mô phỏng các thí nghiệm vật lý với công thức chính xác</p>
      </header>

      <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
        <div className="bg-[#16213e] border-b border-cyan-900/50 px-4 overflow-x-auto">
          <TabsList className="bg-transparent h-12 gap-1">
            {experiments.map((exp) => (
              <TabsTrigger
                key={exp.id}
                value={exp.id}
                className="data-[state=active]:bg-cyan-600 data-[state=active]:text-white text-gray-300 hover:text-cyan-400 transition-colors px-4 py-2 rounded-t-lg"
              >
                {exp.label}
              </TabsTrigger>
            ))}
          </TabsList>
        </div>

        <main className="p-4">
          {experiments.map((exp) => (
            <TabsContent key={exp.id} value={exp.id} className="mt-0">
              <exp.component />
            </TabsContent>
          ))}
        </main>
      </Tabs>
    </div>
  )
}
