"use client"

import { useRef, useEffect, useState, useCallback } from "react"
import { Zap, RotateCcw, SkipBack, Pause, Play, SkipForward } from "lucide-react"

const MAX_TIME = 20

export default function HarmonicOscillation() {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const [isPlaying, setIsPlaying] = useState(false)
  const [time, setTime] = useState(0)

  const [showGraph, setShowGraph] = useState({ x: true, v: false, a: false })

  const [params, setParams] = useState({
    amplitude: 100,
    frequency: 0.5,
    phase: 0,
  })

  const animationRef = useRef<number>()
  const timeRef = useRef(0)

  const omega = 2 * Math.PI * params.frequency
  const dt = 0.016

  const calculateState = useCallback(
    (t: number) => {
      const x = params.amplitude * Math.cos(omega * t + params.phase)
      const v = -params.amplitude * omega * Math.sin(omega * t + params.phase)
      const a = -params.amplitude * omega * omega * Math.cos(omega * t + params.phase)
      return { x, v, a }
    },
    [params.amplitude, omega, params.phase],
  )

  const draw = useCallback(
    (ctx: CanvasRenderingContext2D, t: number) => {
      const width = ctx.canvas.width
      const height = ctx.canvas.height
      const centerX = width / 3
      const centerY = height / 2

      ctx.fillStyle = "#0f172a"
      ctx.fillRect(0, 0, width, height)

      // Grid
      ctx.strokeStyle = "#1e293b"
      ctx.lineWidth = 0.5
      for (let i = 0; i < width; i += 40) {
        ctx.beginPath()
        ctx.moveTo(i, 0)
        ctx.lineTo(i, height)
        ctx.stroke()
      }
      for (let i = 0; i < height; i += 40) {
        ctx.beginPath()
        ctx.moveTo(0, i)
        ctx.lineTo(width, i)
        ctx.stroke()
      }

      const state = calculateState(t)
      const angle = omega * t + params.phase

      // Circle
      const radius = 80
      ctx.strokeStyle = "#4ade80"
      ctx.lineWidth = 2
      ctx.beginPath()
      ctx.arc(centerX, centerY, radius, 0, 2 * Math.PI)
      ctx.stroke()

      // Axes
      ctx.strokeStyle = "#334155"
      ctx.lineWidth = 1
      ctx.beginPath()
      ctx.moveTo(centerX - radius - 20, centerY)
      ctx.lineTo(centerX + radius + 20, centerY)
      ctx.moveTo(centerX, centerY - radius - 20)
      ctx.lineTo(centerX, centerY + radius + 20)
      ctx.stroke()

      // Vector
      const vectorX = centerX + radius * Math.cos(angle)
      const vectorY = centerY - radius * Math.sin(angle)

      ctx.strokeStyle = "#f97316"
      ctx.lineWidth = 3
      ctx.beginPath()
      ctx.moveTo(centerX, centerY)
      ctx.lineTo(vectorX, vectorY)
      ctx.stroke()

      ctx.fillStyle = "#f97316"
      ctx.beginPath()
      ctx.arc(vectorX, vectorY, 6, 0, 2 * Math.PI)
      ctx.fill()

      // Projection
      const projX = centerX + radius * Math.cos(angle)
      ctx.strokeStyle = "#22d3ee"
      ctx.setLineDash([5, 5])
      ctx.beginPath()
      ctx.moveTo(vectorX, vectorY)
      ctx.lineTo(projX, centerY)
      ctx.stroke()
      ctx.setLineDash([])

      ctx.fillStyle = "#22d3ee"
      ctx.beginPath()
      ctx.arc(projX, centerY, 6, 0, 2 * Math.PI)
      ctx.fill()

      // Wave graph
      const waveStartX = centerX + radius + 60
      const waveWidth = width - waveStartX - 20

      ctx.strokeStyle = "#334155"
      ctx.lineWidth = 1
      ctx.beginPath()
      ctx.moveTo(waveStartX, centerY)
      ctx.lineTo(width, centerY)
      ctx.stroke()

      const drawCurve = (color: string, getValue: (waveT: number) => number, scaleFactor = 1) => {
        ctx.strokeStyle = color
        ctx.lineWidth = 2
        ctx.beginPath()
        for (let i = 0; i <= waveWidth; i++) {
          const waveT = t - ((i / waveWidth) * 4 * Math.PI) / omega
          const rawVal = getValue(waveT)
          const y = centerY - ((rawVal * radius) / 100) * scaleFactor

          if (i === 0) ctx.moveTo(waveStartX + i, y)
          else ctx.lineTo(waveStartX + i, y)
        }
        ctx.stroke()
      }

      if (showGraph.x) {
        drawCurve("#22d3ee", (wt) => params.amplitude * Math.cos(omega * wt + params.phase))
      }

      if (showGraph.v) {
        drawCurve("#4ade80", (wt) => -params.amplitude * omega * Math.sin(omega * wt + params.phase), 1 / omega)
      }

      if (showGraph.a) {
        drawCurve(
          "#f87171",
          (wt) => -params.amplitude * omega * omega * Math.cos(omega * wt + params.phase),
          1 / (omega * omega),
        )
      }

      if (showGraph.x) {
        ctx.strokeStyle = "#22d3ee"
        ctx.setLineDash([3, 3])
        ctx.beginPath()
        ctx.moveTo(projX, centerY)
        ctx.lineTo(waveStartX, centerY - (state.x * radius) / 100)
        ctx.stroke()
        ctx.setLineDash([])

        ctx.fillStyle = "#22d3ee"
        ctx.beginPath()
        ctx.arc(waveStartX, centerY - (state.x * radius) / 100, 5, 0, 2 * Math.PI)
        ctx.fill()
      }

      // Labels
      ctx.fillStyle = "#94a3b8"
      ctx.font = "12px monospace"
      ctx.fillText("Vòng tròn lượng giác", centerX - 60, centerY + radius + 30)
      ctx.fillText("Đồ thị theo thời gian", waveStartX + waveWidth / 2 - 60, centerY + radius + 30)

      // Info panel
      ctx.fillStyle = "rgba(15, 23, 42, 0.8)"
      ctx.fillRect(10, 10, 150, 90)
      ctx.strokeStyle = "rgba(56, 189, 248, 0.3)"
      ctx.strokeRect(10, 10, 150, 90)

      ctx.font = "13px monospace"
      ctx.fillStyle = "#e2e8f0"
      ctx.fillText(`t = ${t.toFixed(2)} s`, 20, 30)
      ctx.fillStyle = "#22d3ee"
      ctx.fillText(`x = ${state.x.toFixed(1)} cm`, 20, 50)
      if (showGraph.v) {
        ctx.fillStyle = "#4ade80"
        ctx.fillText(`v = ${state.v.toFixed(1)} cm/s`, 20, 70)
      }
      if (showGraph.a) {
        ctx.fillStyle = "#f87171"
        ctx.fillText(`a = ${state.a.toFixed(1)} cm/s²`, 20, 90)
      }

      ctx.fillStyle = "rgba(255, 255, 255, 0.15)"
      ctx.font = "12px monospace"
      ctx.textAlign = "right"
      ctx.fillText("Hô Hoàng Anh A1K50", width - 15, height - 15)
      ctx.textAlign = "left"
    },
    [params.amplitude, params.phase, omega, calculateState, showGraph],
  )

  const animate = useCallback(() => {
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return

    timeRef.current += dt
    if (timeRef.current > MAX_TIME) timeRef.current = MAX_TIME
    setTime(timeRef.current)
    draw(ctx, timeRef.current)

    if (isPlaying && timeRef.current < MAX_TIME) {
      animationRef.current = requestAnimationFrame(animate)
    } else if (timeRef.current >= MAX_TIME) {
      setIsPlaying(false)
    }
  }, [isPlaying, draw])

  useEffect(() => {
    if (isPlaying) {
      animationRef.current = requestAnimationFrame(animate)
    }
    return () => {
      if (animationRef.current) cancelAnimationFrame(animationRef.current)
    }
  }, [isPlaying, animate])

  useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return
    draw(ctx, timeRef.current)
  }, [params, draw, showGraph])

  const handleStep = (direction: number) => {
    setIsPlaying(false)
    timeRef.current = Math.max(0, Math.min(MAX_TIME, timeRef.current + dt * direction * 10))
    setTime(timeRef.current)
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return
    draw(ctx, timeRef.current)
  }

  const handleReset = () => {
    setIsPlaying(false)
    timeRef.current = 0
    setTime(0)
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return
    draw(ctx, 0)
  }

  const handleTimelineChange = (newTime: number) => {
    setIsPlaying(false)
    timeRef.current = newTime
    setTime(newTime)
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return
    draw(ctx, newTime)
  }

  const state = calculateState(time)
  const kineticEnergy = (0.5 * state.v * state.v) / 10000
  const potentialEnergy = (0.5 * omega * omega * state.x * state.x) / 10000

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-4">
      <div className="lg:col-span-2 space-y-4">
        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50 relative">
          {/* Toggle buttons */}
          <div className="absolute top-4 right-4 flex gap-2 z-10 bg-[#0f172a]/80 p-1 rounded-lg backdrop-blur-sm border border-slate-700">
            <button
              onClick={() => setShowGraph((p) => ({ ...p, x: !p.x }))}
              className={`px-2 py-1 text-[10px] font-bold rounded transition-all ${showGraph.x ? "bg-cyan-500 text-white" : "bg-slate-700 text-slate-400"}`}
            >
              x(t)
            </button>
            <button
              onClick={() => setShowGraph((p) => ({ ...p, v: !p.v }))}
              className={`px-2 py-1 text-[10px] font-bold rounded transition-all ${showGraph.v ? "bg-green-500 text-white" : "bg-slate-700 text-slate-400"}`}
            >
              v(t)
            </button>
            <button
              onClick={() => setShowGraph((p) => ({ ...p, a: !p.a }))}
              className={`px-2 py-1 text-[10px] font-bold rounded transition-all ${showGraph.a ? "bg-red-500 text-white" : "bg-slate-700 text-slate-400"}`}
            >
              a(t)
            </button>
          </div>

          <canvas ref={canvasRef} width={800} height={400} className="w-full rounded-lg bg-[#0f172a]" />
        </div>

        <div className="bg-[#1e293b] rounded-xl p-3 border border-slate-700/50 flex items-center gap-3">
          <div className="flex items-center gap-2">
            <button
              onClick={handleReset}
              className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
              title="Reset"
            >
              <RotateCcw size={20} />
            </button>
            <button
              onClick={() => handleStep(-1)}
              className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
              title="Lùi"
            >
              <SkipBack size={20} />
            </button>
            <button
              onClick={() => setIsPlaying(!isPlaying)}
              className={`w-14 h-14 rounded-2xl flex items-center justify-center transition-all shadow-lg active:scale-95 border border-transparent ${
                isPlaying
                  ? "bg-amber-500 hover:bg-amber-400 shadow-amber-900/20"
                  : "bg-cyan-500 hover:bg-cyan-400 shadow-cyan-500/30"
              } text-white`}
              title={isPlaying ? "Dừng" : "Chạy"}
            >
              {isPlaying ? (
                <Pause size={28} fill="currentColor" />
              ) : (
                <Play size={28} fill="currentColor" className="ml-1" />
              )}
            </button>
            <button
              onClick={() => handleStep(1)}
              className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
              title="Tiến"
            >
              <SkipForward size={20} />
            </button>
          </div>

          <span className="text-xs text-slate-400 font-mono w-8">0s</span>
          <input
            type="range"
            min="0"
            max={MAX_TIME}
            step="0.01"
            value={time}
            onChange={(e) => handleTimelineChange(Number(e.target.value))}
            className="flex-1 h-2 bg-slate-700 rounded-lg appearance-none cursor-pointer accent-cyan-500"
          />
          <span className="text-xs text-slate-400 font-mono w-10 text-right">{MAX_TIME}s</span>
        </div>
      </div>

      {/* Cột phải: Parameters */}
      <div className="space-y-4">
        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50">
          <h3 className="text-cyan-400 font-semibold mb-4 text-sm">Thông số dao động</h3>
          <div className="space-y-4">
            <div>
              <div className="flex justify-between text-xs mb-1">
                <label className="text-slate-400">Biên độ A</label>
                <span className="text-cyan-400 font-mono">{params.amplitude} cm</span>
              </div>
              <input
                type="range"
                min="20"
                max="150"
                value={params.amplitude}
                onChange={(e) => setParams({ ...params, amplitude: Number(e.target.value) })}
                className="w-full accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
              />
            </div>
            <div>
              <div className="flex justify-between text-xs mb-1">
                <label className="text-slate-400">Tần số f</label>
                <span className="text-cyan-400 font-mono">{params.frequency} Hz</span>
              </div>
              <input
                type="range"
                min="0.1"
                max="2"
                step="0.1"
                value={params.frequency}
                onChange={(e) => setParams({ ...params, frequency: Number(e.target.value) })}
                className="w-full accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
              />
            </div>
            <div>
              <div className="flex justify-between text-xs mb-1">
                <label className="text-slate-400">Pha ban đầu φ</label>
                <span className="text-cyan-400 font-mono">{params.phase.toFixed(2)} rad</span>
              </div>
              <input
                type="range"
                min="0"
                max={Math.PI * 2}
                step="0.1"
                value={params.phase}
                onChange={(e) => setParams({ ...params, phase: Number(e.target.value) })}
                className="w-full accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
              />
            </div>
          </div>
        </div>

        {/* Energy Bars */}
        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50">
          <h3 className="text-amber-400 font-semibold mb-3 text-sm flex items-center gap-2">
            <Zap size={14} /> Năng lượng
          </h3>
          <div className="space-y-3">
            <div>
              <div className="flex justify-between text-xs mb-1">
                <span className="text-green-400">Động năng Wđ</span>
                <span className="text-green-400 font-mono">{kineticEnergy.toFixed(3)} J</span>
              </div>
              <div className="h-2 bg-slate-700 rounded-full overflow-hidden">
                <div
                  className="h-full bg-green-500 transition-all duration-100"
                  style={{ width: `${(kineticEnergy / (kineticEnergy + potentialEnergy || 1)) * 100}%` }}
                />
              </div>
            </div>
            <div>
              <div className="flex justify-between text-xs mb-1">
                <span className="text-blue-400">Thế năng Wt</span>
                <span className="text-blue-400 font-mono">{potentialEnergy.toFixed(3)} J</span>
              </div>
              <div className="h-2 bg-slate-700 rounded-full overflow-hidden">
                <div
                  className="h-full bg-blue-500 transition-all duration-100"
                  style={{ width: `${(potentialEnergy / (kineticEnergy + potentialEnergy || 1)) * 100}%` }}
                />
              </div>
            </div>
            <div>
              <div className="flex justify-between text-xs mb-1">
                <span className="text-yellow-400">Cơ năng W</span>
                <span className="text-yellow-400 font-mono">{(kineticEnergy + potentialEnergy).toFixed(3)} J</span>
              </div>
              <div className="h-2 bg-yellow-500/30 rounded-full" />
            </div>
          </div>
        </div>

        {/* Formulas */}
        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50">
          <h3 className="text-cyan-400 font-semibold mb-2 text-sm">Công thức</h3>
          <div className="text-xs text-slate-300 space-y-1 font-mono">
            <p className="flex justify-between">
              <span>x = A·cos(ωt + φ)</span> <span className="text-cyan-400">Li độ</span>
            </p>
            <p className="flex justify-between">
              <span>v = -Aω·sin(ωt + φ)</span> <span className="text-green-400">Vận tốc</span>
            </p>
            <p className="flex justify-between">
              <span>a = -Aω²·cos(ωt + φ)</span> <span className="text-red-400">Gia tốc</span>
            </p>
            <div className="w-full h-[1px] bg-slate-700 my-2"></div>
            <p>ω = 2πf = {omega.toFixed(2)} rad/s</p>
            <p>T = 1/f = {(1 / params.frequency).toFixed(2)} s</p>
          </div>
        </div>
      </div>
    </div>
  )
}
