"use client"

import { useRef, useEffect, useState, useCallback } from "react"
import { Play, Pause, RotateCcw, Eye, EyeOff, Waves, SkipBack, SkipForward } from "lucide-react"

const MAX_TIME = 20

export default function WaveInterference() {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const [isPlaying, setIsPlaying] = useState(false)
  const [time, setTime] = useState(0)
  const [showNodalLines, setShowNodalLines] = useState(false)
  const [view3D, setView3D] = useState(false)
  const [params, setParams] = useState({
    frequency: 0.8,
    amplitude: 1.0,
    sourceDistance: 120,
    wavelength: 40,
  })

  const animationRef = useRef<number>()
  const timeRef = useRef(0)

  const omega = 2 * Math.PI * params.frequency
  const k = (2 * Math.PI) / params.wavelength
  const dt = 1 / 60

  const calculateWaveHeight = useCallback(
    (x: number, y: number, s1x: number, s1y: number, s2x: number, s2y: number, t: number) => {
      const d1 = Math.sqrt((x - s1x) ** 2 + (y - s1y) ** 2)
      const d2 = Math.sqrt((x - s2x) ** 2 + (y - s2y) ** 2)

      const amp1 = params.amplitude / Math.sqrt(1 + d1 * 0.02)
      const amp2 = params.amplitude / Math.sqrt(1 + d2 * 0.02)

      const wave1 = amp1 * Math.sin(k * d1 - omega * t)
      const wave2 = amp2 * Math.sin(k * d2 - omega * t)

      return wave1 + wave2
    },
    [k, omega, params.amplitude],
  )

  const getNodalLinePoints = useCallback(
    (
      width: number,
      height: number,
      s1x: number,
      s1y: number,
      s2x: number,
      s2y: number,
      order: number,
    ): { x: number; y: number }[] => {
      const points: { x: number; y: number }[] = []
      const pathDiff = (order + 0.5) * params.wavelength

      for (let x = 0; x < width; x += 3) {
        for (let y = 0; y < height; y += 3) {
          const d1 = Math.sqrt((x - s1x) ** 2 + (y - s1y) ** 2)
          const d2 = Math.sqrt((x - s2x) ** 2 + (y - s2y) ** 2)
          const diff = Math.abs(d1 - d2)

          if (Math.abs(diff - pathDiff) < 3) {
            points.push({ x, y })
          }
        }
      }
      return points
    },
    [params.wavelength],
  )

  const draw = useCallback(
    (ctx: CanvasRenderingContext2D, t: number) => {
      const width = ctx.canvas.width
      const height = ctx.canvas.height
      const centerY = height / 2

      const source1 = { x: 80, y: centerY - params.sourceDistance / 2 }
      const source2 = { x: 80, y: centerY + params.sourceDistance / 2 }

      const imageData = ctx.createImageData(width, height)
      const data = imageData.data

      const resolution = view3D ? 3 : 2

      if (view3D) {
        ctx.fillStyle = "#0f172a"
        ctx.fillRect(0, 0, width, height)

        const perspectiveScale = 0.6
        const horizonY = height * 0.2
        const gridSpacing = 8

        for (let z = 0; z < height * 1.5; z += gridSpacing) {
          const perspY = horizonY + z * perspectiveScale
          if (perspY > height) continue

          const scale = 0.3 + (z / (height * 1.5)) * 0.7

          for (let x = 0; x < width; x += gridSpacing) {
            const worldX = (x - width / 2) / scale + width / 2
            const worldZ = z

            const waveHeight = calculateWaveHeight(worldX, worldZ, source1.x, source1.y, source2.x, source2.y, t)

            const normalizedHeight = (waveHeight + 2 * params.amplitude) / (4 * params.amplitude)

            const r = Math.floor(30 + normalizedHeight * 50)
            const g = Math.floor(100 + normalizedHeight * 155)
            const b = Math.floor(180 + normalizedHeight * 75)

            const screenX = width / 2 + (x - width / 2) * scale
            const screenY = perspY - waveHeight * 15 * scale

            ctx.fillStyle = `rgb(${r}, ${g}, ${b})`
            ctx.fillRect(screenX - 2, screenY - 2, 4, 4)
          }
        }
      } else {
        for (let y = 0; y < height; y += resolution) {
          for (let x = 0; x < width; x += resolution) {
            const waveHeight = calculateWaveHeight(x, y, source1.x, source1.y, source2.x, source2.y, t)

            const maxAmp = 2 * params.amplitude
            const normalizedHeight = (waveHeight + maxAmp) / (2 * maxAmp)

            let r, g, b

            if (normalizedHeight > 0.5) {
              const intensity = (normalizedHeight - 0.5) * 2
              r = Math.floor(50 + intensity * 205)
              g = Math.floor(150 + intensity * 105)
              b = Math.floor(200 + intensity * 55)
            } else {
              const intensity = normalizedHeight * 2
              r = Math.floor(5 + intensity * 45)
              g = Math.floor(20 + intensity * 130)
              b = Math.floor(60 + intensity * 140)
            }

            for (let dy = 0; dy < resolution && y + dy < height; dy++) {
              for (let dx = 0; dx < resolution && x + dx < width; dx++) {
                const idx = ((y + dy) * width + (x + dx)) * 4
                data[idx] = r
                data[idx + 1] = g
                data[idx + 2] = b
                data[idx + 3] = 255
              }
            }
          }
        }

        ctx.putImageData(imageData, 0, 0)
      }

      if (showNodalLines && !view3D) {
        ctx.strokeStyle = "rgba(255, 100, 100, 0.8)"
        ctx.lineWidth = 2
        ctx.setLineDash([5, 5])

        const maxOrder = Math.floor(params.sourceDistance / params.wavelength) + 2
        for (let order = 0; order <= maxOrder; order++) {
          const points = getNodalLinePoints(width, height, source1.x, source1.y, source2.x, source2.y, order)

          if (points.length > 0) {
            const upperPoints = points.filter((p) => p.y < centerY).sort((a, b) => a.x - b.x)
            const lowerPoints = points.filter((p) => p.y >= centerY).sort((a, b) => a.x - b.x)

            if (upperPoints.length > 1) {
              ctx.beginPath()
              ctx.moveTo(upperPoints[0].x, upperPoints[0].y)
              for (let i = 1; i < upperPoints.length; i++) {
                ctx.lineTo(upperPoints[i].x, upperPoints[i].y)
              }
              ctx.stroke()
            }

            if (lowerPoints.length > 1) {
              ctx.beginPath()
              ctx.moveTo(lowerPoints[0].x, lowerPoints[0].y)
              for (let i = 1; i < lowerPoints.length; i++) {
                ctx.lineTo(lowerPoints[i].x, lowerPoints[i].y)
              }
              ctx.stroke()
            }
          }
        }

        ctx.strokeStyle = "rgba(100, 255, 100, 0.8)"
        ctx.setLineDash([8, 4])
        ctx.beginPath()
        ctx.moveTo(source1.x, centerY)
        ctx.lineTo(width, centerY)
        ctx.stroke()

        ctx.setLineDash([])
      }

      // Sources
      const gradient1 = ctx.createRadialGradient(source1.x, source1.y, 0, source1.x, source1.y, 20)
      gradient1.addColorStop(0, "rgba(255, 100, 100, 1)")
      gradient1.addColorStop(0.5, "rgba(255, 100, 100, 0.5)")
      gradient1.addColorStop(1, "rgba(255, 100, 100, 0)")
      ctx.fillStyle = gradient1
      ctx.beginPath()
      ctx.arc(source1.x, source1.y, 20, 0, 2 * Math.PI)
      ctx.fill()

      ctx.fillStyle = "#ff6b6b"
      ctx.beginPath()
      ctx.arc(source1.x, source1.y, 8, 0, 2 * Math.PI)
      ctx.fill()

      const gradient2 = ctx.createRadialGradient(source2.x, source2.y, 0, source2.x, source2.y, 20)
      gradient2.addColorStop(0, "rgba(255, 100, 100, 1)")
      gradient2.addColorStop(0.5, "rgba(255, 100, 100, 0.5)")
      gradient2.addColorStop(1, "rgba(255, 100, 100, 0)")
      ctx.fillStyle = gradient2
      ctx.beginPath()
      ctx.arc(source2.x, source2.y, 20, 0, 2 * Math.PI)
      ctx.fill()

      ctx.fillStyle = "#ff6b6b"
      ctx.beginPath()
      ctx.arc(source2.x, source2.y, 8, 0, 2 * Math.PI)
      ctx.fill()

      ctx.fillStyle = "#fff"
      ctx.font = "bold 14px monospace"
      ctx.fillText("S₁", source1.x - 25, source1.y + 5)
      ctx.fillText("S₂", source2.x - 25, source2.y + 5)

      // Info panel
      ctx.fillStyle = "rgba(15, 23, 42, 0.8)"
      ctx.fillRect(10, 10, 160, 100)
      ctx.strokeStyle = "rgba(56, 189, 248, 0.3)"
      ctx.strokeRect(10, 10, 160, 100)

      ctx.fillStyle = "#4ade80"
      ctx.font = "13px monospace"
      ctx.fillText(`t = ${t.toFixed(2)} s`, 20, 32)
      ctx.fillText(`f = ${params.frequency.toFixed(1)} Hz`, 20, 52)
      ctx.fillText(`λ = ${params.wavelength} px`, 20, 72)
      ctx.fillText(`d = ${params.sourceDistance} px`, 20, 92)

      // Legend
      ctx.fillStyle = "rgba(15, 23, 42, 0.8)"
      ctx.fillRect(ctx.canvas.width - 170, 10, 160, 70)
      ctx.strokeStyle = "rgba(56, 189, 248, 0.3)"
      ctx.strokeRect(ctx.canvas.width - 170, 10, 160, 70)

      ctx.fillStyle = "#22d3ee"
      ctx.fillRect(ctx.canvas.width - 160, 25, 20, 12)
      ctx.fillStyle = "#fff"
      ctx.font = "12px monospace"
      ctx.fillText("Cực đại (sáng)", ctx.canvas.width - 135, 35)

      ctx.fillStyle = "#0a2540"
      ctx.fillRect(ctx.canvas.width - 160, 45, 20, 12)
      ctx.fillStyle = "#fff"
      ctx.fillText("Cực tiểu (tối)", ctx.canvas.width - 135, 55)

      ctx.fillStyle = "rgba(255, 255, 255, 0.15)"
      ctx.font = "12px monospace"
      ctx.textAlign = "right"
      ctx.fillText("Hô Hoàng Anh A1K50", width - 15, height - 15)
      ctx.textAlign = "left"
    },
    [
      params.sourceDistance,
      params.frequency,
      params.wavelength,
      params.amplitude,
      view3D,
      showNodalLines,
      calculateWaveHeight,
      getNodalLinePoints,
    ],
  )

  const animate = useCallback(() => {
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return

    timeRef.current += dt
    if (timeRef.current > MAX_TIME) timeRef.current = MAX_TIME
    setTime(timeRef.current)
    draw(ctx, timeRef.current)

    if (isPlaying && timeRef.current < MAX_TIME) {
      animationRef.current = requestAnimationFrame(animate)
    } else if (timeRef.current >= MAX_TIME) {
      setIsPlaying(false)
    }
  }, [isPlaying, draw, dt])

  useEffect(() => {
    if (isPlaying) {
      animationRef.current = requestAnimationFrame(animate)
    }
    return () => {
      if (animationRef.current) cancelAnimationFrame(animationRef.current)
    }
  }, [isPlaying, animate])

  useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return
    draw(ctx, timeRef.current)
  }, [params, view3D, showNodalLines, draw])

  const handleReset = () => {
    setIsPlaying(false)
    timeRef.current = 0
    setTime(0)
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return
    draw(ctx, 0)
  }

  const handleStep = (direction: number) => {
    setIsPlaying(false)
    timeRef.current = Math.max(0, Math.min(MAX_TIME, timeRef.current + dt * direction * 10))
    setTime(timeRef.current)
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return
    draw(ctx, timeRef.current)
  }

  const handleTimelineChange = (newTime: number) => {
    setIsPlaying(false)
    timeRef.current = newTime
    setTime(newTime)
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return
    draw(ctx, newTime)
  }

  return (
    <div className="grid grid-cols-1 lg:grid-cols-4 gap-4">
      <div className="lg:col-span-3 space-y-4">
        <div className="bg-[#1e293b] rounded-xl p-1 border border-slate-700/50 shadow-lg">
          <canvas ref={canvasRef} width={800} height={450} className="w-full rounded-lg bg-[#0f172a]" />
        </div>

        <div className="bg-[#1e293b] rounded-xl p-3 border border-slate-700/50 flex items-center gap-3">
          <div className="flex items-center gap-2">
            <button
              onClick={handleReset}
              className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
              title="Reset"
            >
              <RotateCcw size={20} />
            </button>
            <button
              onClick={() => handleStep(-1)}
              className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
              title="Lùi"
            >
              <SkipBack size={20} />
            </button>
            <button
              onClick={() => setIsPlaying(!isPlaying)}
              className={`w-14 h-14 rounded-2xl flex items-center justify-center transition-all shadow-lg active:scale-95 border border-transparent ${
                isPlaying
                  ? "bg-amber-500 hover:bg-amber-400 shadow-amber-900/20"
                  : "bg-cyan-500 hover:bg-cyan-400 shadow-cyan-500/30"
              } text-white`}
              title={isPlaying ? "Dừng" : "Chạy"}
            >
              {isPlaying ? (
                <Pause size={28} fill="currentColor" />
              ) : (
                <Play size={28} fill="currentColor" className="ml-1" />
              )}
            </button>
            <button
              onClick={() => handleStep(1)}
              className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
              title="Tiến"
            >
              <SkipForward size={20} />
            </button>
          </div>

          <span className="text-xs text-slate-400 font-mono w-8">0s</span>
          <input
            type="range"
            min="0"
            max={MAX_TIME}
            step="0.01"
            value={time}
            onChange={(e) => handleTimelineChange(Number(e.target.value))}
            className="flex-1 h-2 bg-slate-700 rounded-lg appearance-none cursor-pointer accent-cyan-500"
          />
          <span className="text-xs text-slate-400 font-mono w-10 text-right">{MAX_TIME}s</span>
        </div>
      </div>

      <div className="space-y-4">
        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50">
          <h3 className="text-cyan-400 font-bold mb-4 flex items-center gap-2 text-sm">
            <Waves className="w-4 h-4" />
            Thông số sóng
          </h3>
          <div className="space-y-4">
            <div>
              <div className="flex justify-between text-xs mb-1">
                <label className="text-slate-400">Tần số f</label>
                <span className="text-cyan-400 font-mono">{params.frequency.toFixed(1)} Hz</span>
              </div>
              <input
                type="range"
                min="0.3"
                max="2.0"
                step="0.1"
                value={params.frequency}
                onChange={(e) => setParams({ ...params, frequency: Number(e.target.value) })}
                className="w-full h-2 bg-slate-700 rounded-lg appearance-none cursor-pointer accent-cyan-500"
              />
            </div>

            <div>
              <div className="flex justify-between text-xs mb-1">
                <label className="text-slate-400">Biên độ A</label>
                <span className="text-cyan-400 font-mono">{params.amplitude.toFixed(1)}</span>
              </div>
              <input
                type="range"
                min="0.5"
                max="2.0"
                step="0.1"
                value={params.amplitude}
                onChange={(e) => setParams({ ...params, amplitude: Number(e.target.value) })}
                className="w-full h-2 bg-slate-700 rounded-lg appearance-none cursor-pointer accent-cyan-500"
              />
            </div>

            <div>
              <div className="flex justify-between text-xs mb-1">
                <label className="text-slate-400">Bước sóng λ</label>
                <span className="text-cyan-400 font-mono">{params.wavelength} px</span>
              </div>
              <input
                type="range"
                min="20"
                max="80"
                step="5"
                value={params.wavelength}
                onChange={(e) => setParams({ ...params, wavelength: Number(e.target.value) })}
                className="w-full h-2 bg-slate-700 rounded-lg appearance-none cursor-pointer accent-cyan-500"
              />
            </div>

            <div>
              <div className="flex justify-between text-xs mb-1">
                <label className="text-slate-400">Khoảng cách nguồn d</label>
                <span className="text-cyan-400 font-mono">{params.sourceDistance} px</span>
              </div>
              <input
                type="range"
                min="40"
                max="200"
                step="10"
                value={params.sourceDistance}
                onChange={(e) => setParams({ ...params, sourceDistance: Number(e.target.value) })}
                className="w-full h-2 bg-slate-700 rounded-lg appearance-none cursor-pointer accent-cyan-500"
              />
            </div>
          </div>
        </div>

        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50">
          <h3 className="text-cyan-400 font-bold mb-3 text-sm">Tùy chọn hiển thị</h3>
          <div className="space-y-3">
            <label className="flex items-center justify-between cursor-pointer">
              <span className="text-slate-300 text-xs flex items-center gap-2">
                {showNodalLines ? <Eye className="w-4 h-4" /> : <EyeOff className="w-4 h-4" />}
                Đường nút (Nodal Lines)
              </span>
              <div
                onClick={() => setShowNodalLines(!showNodalLines)}
                className={`w-10 h-5 rounded-full transition-all cursor-pointer ${
                  showNodalLines ? "bg-cyan-600" : "bg-slate-600"
                }`}
              >
                <div
                  className={`w-4 h-4 bg-white rounded-full shadow-md transform transition-transform mt-0.5 ${
                    showNodalLines ? "translate-x-5" : "translate-x-0.5"
                  }`}
                />
              </div>
            </label>

            <label className="flex items-center justify-between cursor-pointer">
              <span className="text-slate-300 text-xs">Chế độ 3D</span>
              <div
                onClick={() => setView3D(!view3D)}
                className={`w-10 h-5 rounded-full transition-all cursor-pointer ${
                  view3D ? "bg-cyan-600" : "bg-slate-600"
                }`}
              >
                <div
                  className={`w-4 h-4 bg-white rounded-full shadow-md transform transition-transform mt-0.5 ${
                    view3D ? "translate-x-5" : "translate-x-0.5"
                  }`}
                />
              </div>
            </label>
          </div>
        </div>

        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50">
          <h3 className="text-cyan-400 font-bold mb-3 text-sm">Điều kiện giao thoa</h3>
          <div className="text-xs text-slate-300 space-y-2">
            <div className="flex items-start gap-2">
              <div className="w-3 h-3 bg-cyan-400 rounded-full mt-0.5 flex-shrink-0" />
              <p>
                <span className="text-cyan-400 font-semibold">Cực đại:</span>
                <br />
                <span className="font-mono text-[10px]">Δd = kλ (k = 0, ±1, ±2...)</span>
              </p>
            </div>
            <div className="flex items-start gap-2">
              <div className="w-3 h-3 bg-slate-600 rounded-full mt-0.5 flex-shrink-0" />
              <p>
                <span className="text-slate-400 font-semibold">Cực tiểu:</span>
                <br />
                <span className="font-mono text-[10px]">Δd = (k+½)λ</span>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
